.row-title {
  text-align: left;
  margin-left: 10px;
}

.row-posters {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 8px 15px;
  margin-bottom: 30px;
}

.row-poster::-webkit-scrollbar {
  display: none;
