.row-poster {
  object-fit: contain;
  width: 100%;
  max-height: 150px;
  transition: transform 450ms;
  margin-right: 10px;
  cursor:pointer;
}

.row-poster:hover {
  transform: scale(1.08);
}

.row-posterLarge {
  max-height: 250px;
}

